import NavBar from "./common/NavBar";
import { Route, Switch, Redirect } from "react-router-dom";
import Home from "./components/Home";
import HowToGoToHeaven from "./components/HowToGoToHeaven";
import NotFound from "./components/notFound";
import Genesis from "./components/1Genesis";
import Exodus from "./components/2Exodus";
import Leviticus from "./components/3Leviticus";
import Numbers from "./components/4Numbers";
import Deuteronomy from "./components/5Deuteronomy";
import Joshua from "./components/6Joshua";
import Judges from "./components/7Judges";
import Ruth from "./components/8Ruth";
import Samuel1 from "./components/9Samuel1";

function App() {
  return (
    <>
      <NavBar />
      <Switch>
        <Route path="/bible-app" component={Home} />
        <Route path="/Genesis" component={Genesis} />
        <Route path="/Exodus" component={Exodus} />
        <Route path="/Leviticus" component={Leviticus} />
        <Route path="/Numbers" component={Numbers} />
        <Route path="/Deuteronomy" component={Deuteronomy} />
        <Route path="/Joshua" component={Joshua} />
        <Route path="/Judges" component={Judges} />
        <Route path="/Ruth" component={Ruth} />
        <Route path="/Samuel1" component={Samuel1} />
        <Route path="/HowToGoToHeaven" component={HowToGoToHeaven} />
        <Route path="/not-found" component={NotFound} />
        <Redirect exact from="/" to="/bible-app" />
        <Redirect to="/not-found" />
      </Switch>
    </>
  );
}

export default App;
