import { NavLink, Link } from "react-router-dom";

function NavBar() {
  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
      <div className="container">
        <NavLink className="navbar-brand" to="/bible-app">
          Home
        </NavLink>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item dropdown">
              <a
                className="nav-link dropdown-toggle"
                href="#"
                id="navbarDropdown"
                role="button"
                data-bs-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                Chapters
              </a>
              <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                <Link className="dropdown-item" to="/Genesis">
                  Genesis
                </Link>
                <div className="dropdown-divider"></div>
                <Link className="dropdown-item" to="/Exodus">
                  Exodus
                </Link>
                <div className="dropdown-divider"></div>
                <Link className="dropdown-item" to="/Leviticus">
                  Leviticus
                </Link>
                <div className="dropdown-divider"></div>
                <Link className="dropdown-item" to="/Numbers">
                  Numbers
                </Link>
                <div className="dropdown-divider"></div>
                <Link className="dropdown-item" to="/Deuteronomy">
                  Deuteronomy
                </Link>
                <div className="dropdown-divider"></div>
                <Link className="dropdown-item" to="/Joshua">
                  Joshua
                </Link>
                <div className="dropdown-divider"></div>
                <Link className="dropdown-item" to="/Judges">
                  Judges
                </Link>
                <div className="dropdown-divider"></div>
                <Link className="dropdown-item" to="/Ruth">
                  Ruth
                </Link>
                <div className="dropdown-divider"></div>
                <Link className="dropdown-item" to="/Samuel1">
                  1 Samuel
                </Link>
              </div>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/HowToGoToHeaven">
                How to go to Heaven the EASY Way
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default NavBar;
